@keyframes mercanie{
0%{
	 box-shadow: 0 0 40px #ffaa2f;
}

36%{
	 box-shadow: 0 0 40px #584948;

}

36.5%{
	 box-shadow: 0 0 40px #ffaa2f;
}
37%{
	 box-shadow: 0 0 26px #584948;
	 
}
37.5%{
	 box-shadow: 0 0 40px #ffaa2f;
}


100%{
	 box-shadow: 0 0 40px #ffaa2f;
}

}


@keyframes mercanie1{
0%{
	 box-shadow: 0 0 40px #ffaa2f;
}

10%{
	 box-shadow: 0 0 20px #ffaa2f;
}

20%{
	 box-shadow: 0 0 40px #ffaa2f;

}

30%{
	 box-shadow: 0 0 20px #ffaa2f;
}

40%{
	 box-shadow: 0 0 40px #ffaa2f;

}

50%{
	 box-shadow: 0 0 10px #ffaa2f;
}

60%{
	 box-shadow: 0 0 40px #ffaa2f;

}

70%{
	 box-shadow: 0 0 20px #ffaa2f;
}

80%{
	 box-shadow: 0 0 40px #ffaa2f;

}

90%{
	 box-shadow: 0 0 20px #ffaa2f;
}

100%{
	 box-shadow: 0 0 40px #ffaa2f;

}

}









